import { Fragment } from 'react'
import { Table, Button, Image, Tag, Popconfirm } from 'antd'
import { dateFormat } from 'Utils'

const GoodsTable = props => {
    const columns = [{
        title: '商品名称',
        dataIndex: 'name',
        key: 'name'
    }, {
        title: '商品图片',
        dataIndex: 'pic',
        key: 'pic',
        render: item => <Image
            width={ 36 }
            height={ 36 }
            src={ item }
            fallback="data:image/png;base64,"
        />
    }, {
        title: '商品类型',
        dataIndex: 'type',
        key: 'type',
        render: item => <Tag color={ item === '果茶' ? '#87d068' : item === '咖啡' ? '#FF7F50' : '#d3adf7' }>{ item }</Tag>
    }, {
        title: '规格',
        dataIndex: 'specifications',
        key: 'specifications',
        render: (item, data) => item.split(',').map(tag => <Tag key={ tag } color="purple">{ tag + ':' + (tag === '中杯' ? data.medium_price : data.big_price) + '元' }</Tag>)
    }, {
        title: '上架时间',
        dataIndex: 'create_time',
        key: 'create_time',
        render: item => dateFormat(Date.parse(item), 'yyyy-MM-dd hh:mm:ss')
    }]

    // 权限处理
    if ( props.admin === 1 ) {
        columns.push({
            title: '操作',
            dataIndex: 'operation',
            key: 'operation',
            render: (value, data, index) => {
                return <Fragment>
                    {/* 修改 */}
                    <Button type="primary" shape="circle" onClick={() => props.edit(index)}><i className="iconfont edit"></i></Button>
    
                    {/* 删除 */}
                    <Popconfirm
                        title={`你确定要删除'${ data.name }'这一项吗`}
                        onConfirm={() => props.delete(data.id)}
                        okText="确定"
                        cancelText="取消"
                    >
                        <Button type="danger" shape="circle"><i className="iconfont shanchu"></i></Button>
                    </Popconfirm>
    
                    {/* 上下架 */}
                    <Button type="primary" onClick={() => props.changeOnSale(data.id, data.on_sale)}>{ data.on_sale === 1 ? '下架' : '上架' }</Button>
                </Fragment>
            }
        })
    } else if ( props.admin === 2 ) {
        columns.push({
            title: '操作',
            dataIndex: 'operation',
            key: 'operation',
            render: (value, data, index) => <Button type="primary" onClick={() => props.changeOnSale(data.id, data.on_sale)}>{ data.on_sale === 1 ? '下架' : '上架' }</Button>      
        })
    }

    return (
        <Table 
            dataSource={ props.list } 
            columns={ columns } 
            rowKey="id" 
            pagination= {{
                position: ['bottomCenter'],
                current: props.current,
                total: props.total,
                defaultPageSize: props.pageSize
            }}
            onChange={page => props.changePaging(page)}
        />
    )
}

export default GoodsTable